import axios from 'axios';
import { API_URL } from './api';

const URL = `${API_URL}/albums`;

export default {
  get() {
    return axios.get(URL)
      .then(r => r.data)
      .catch(err => {
        throw err.response.data;
      });
  },
  add(album) {
    return axios.post(URL, album)
      .then(r => r.data)
      .catch(err => {
        throw err.response.data;
      });
  },
  remove(id) {
    return axios.delete(`${URL}/${id}`)
      .then(r => r.data)
      .catch(err => {
        throw err.response.data;
      });
  }
};


