import * as actions from './constants';
import api from '../api/authApi';

export function checkForToken() {

  return dispatch => {
    const token = localStorage.token || 'ABC';
    if(!token) return;

    api.verify(token)
      .then(() => api.getUser())
      .then(
        user => {
          dispatch({ type: actions.FETCHED_USER, payload: user });
        },
        ({ error }) => {
          dispatch({ type: actions.FETCHED_USER_FAILED, payload: error });
        }
      );

  };
}


