import axios from 'axios';
import { API_URL } from './api';

const URL = `${API_URL}/albums`;

export default {
  get(id) {
    return axios.get(`${URL}/${id}`)
      .then(r => r.data)
      .catch(err => {
        throw err.response.data;
      });
  },
  addImage(albumId, image) {
    return axios.post(`${URL}/${albumId}/images`, image)
      .then(r => r.data)
      .catch(err => {
        throw err.response.data;
      });
  },
  removeImage(albumId, id) {
    return axios.delete(`${URL}/${albumId}/images/${id}`)
      .then(r => r.data)
      .catch(err => {
        throw err.response.data;
      });
  }
};


